
.item-header {
    margin-top: 80px;
    color: #f13c20;
}

.white-bg {
    padding-top: 80px;
    background-color: #FFFFFF;
    padding-bottom: 160px;
}

.text-below {
    color: #f13c20;
    padding-top: 35px;
    display: inline-block;
    font-weight: 600;
}

.about-me {
    color: #f13c20;
    display: inline-block;
    font-size: 18px;
    font-weight: 700;
    width: 80%;
    line-height: 200%;
}

.icon-bg {
    height: 100px;
    width: 100px;
    border-radius: 100px;
    color: #FFFFFF;
    background-color: #f13c20;
    transition: 0.6s;
}

.icon-bg:hover {
    animation-fill-mode: forwards;
    color: #f13c20;
    background-color: #c5cbe3;
}

.small-icon-bg {
    background-color: #f13c20;
    color: #ffffff;
    transition: 0.6s
}

.small-icon-bg:hover {
    cursor: pointer;
    animation-fill-mode: forwards;
    color: #f13c20;
    background-color: #c5cbe3;
}